@import url(https://fonts.googleapis.com/css?family=Poppins:regular,bold,italic&subset=latin,latin-ext);
:root {
	font-family: 'Poppins';
	font-size: 16px;
}

.light-theme {
	--text: #222;
	--bg: #efefef;
}

.dark-theme {
	--text: #fff;
	--bg: #181818;
}

html, body {
	margin: 0;
	padding: 0;
}

main {
	height: 100%;
	background: var(--bg);
	color: var(--text);
	transition: background 500ms ease-in-out, color 1000ms ease-in-out;
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

a {
	color: currentColor;
	text-decoration: none;
}

body::-webkit-scrollbar {
	width: 0.25rem;
}

body::-webkit-scrollbar-track {
	background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
	background: #6649b8;
}
.navbar {
	width: 100%;
	background: #000;
	color: #fff;
}

.navbar-nav {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: left;
	        justify-content: left;
	height: 100%;
}

.nav-item {
	padding: 1em;
	width: 6em;
	text-align: center;
	transition: 1000ms ease;
}

.nav-item:last-child {
	margin-left: auto;
}

.nav-item:hover {
	background: #222;
}
